import { initPageLoader } from './components/PageLoader';
import { initCaptchaBanner } from './scripts/adCaptchaBanner';
import { initGiftsOnYoutubePage } from './scripts/giftsOnYoutubePageContent';
import { initTimeSaver } from './scripts/initTimeSaver';
import { initSubscriptionExpiringOnYoutubePage } from './scripts/subscriptionExpiringOnYoutubePageContent';
import { initVideoOpener } from './scripts/videoOpener';
import * as youboostNeoPremium from './scripts/youboostNeoPremium';
import { injectScript } from './utils/injectScript';
import './i18n';

export { youboostNeoPremium };

try {
  initVideoOpener();
  initPageLoader();
  initTimeSaver();
  initCaptchaBanner();
  initGiftsOnYoutubePage();
  initSubscriptionExpiringOnYoutubePage();
  if (
    process.env.EXTENSION_CODE === 'youboost-neo' ||
    process.env.EXTENSION_CODE === 'youboost' ||
    process.env.EXTENSION_CODE === 'naruzhuv2'
  ) {
    injectScript('scripts/updateYtbPlayerCurrentQuality.js');
  }
  if (process.env.EXTENSION_CODE === 'youboost') {
    void injectScript('scripts/preRollInPage.js');
  }
} catch (error) {
  console.error('CONTENT SCRIPT INIT FAILED');
  console.error(error);
}
