import { ToastOptions } from '../types';
import { createCloseButton } from './createCloseButton';

export function createToastContent(
  options: ToastOptions,
  hide: () => void,
): HTMLElement {
  const content = document.createElement('div');
  content.classList.add('custom-toast-wrapper__content');

  // Header
  const header = document.createElement('div');
  header.classList.add('custom-toast-wrapper__header');
  if (options.title) {
    const headerText = document.createElement('h3');
    headerText.textContent = options.title;

    const closeButton = createCloseButton(hide, options.closeIconSrc);

    header.appendChild(headerText);
    header.appendChild(closeButton);
    content.appendChild(header);
  } else {
    header.classList.add('custom-toast-wrapper__header--end');

    const closeButton = createCloseButton(hide, options.closeIconSrc);

    header.appendChild(closeButton);
    content.appendChild(header);
  }

  // Description
  const description = document.createElement('p');
  description.textContent = options.text;
  description.classList.add('custom-toast-wrapper__description');
  content.appendChild(description);

  // Buttons
  if (options.buttons && options.buttons.length > 0) {
    const buttonsContainer = document.createElement('div');
    buttonsContainer.classList.add('custom-toast-wrapper__buttons');

    options.buttons.forEach((button) => {
      const buttonElement = document.createElement('button');
      buttonElement.textContent = button.text;

      if (button.className) {
        buttonElement.classList.add(button.className);
      }

      if (button.accent) {
        buttonElement.classList.add('accent');
      }

      buttonElement.addEventListener('click', async (e) => {
        e.stopPropagation();
        await button.onClick();
        hide();
      });

      buttonsContainer.appendChild(buttonElement);
    });

    content.appendChild(buttonsContainer);
  }

  return content;
}
