import { Actions } from '@app/actions/shared';
import {
  isMessageOfType,
  type VideoOpenerReadyMessage,
} from '@app/messages/shared';
import { initVideoLoader } from '../components/VideoLoader/VideoLoader';
import { Events, type ShowPageLoaderEvent } from '@app/events';
import { browser_api } from '@app/browser_api';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

import { waitForBridgeStorageLoaded } from '@bridgeStore/waitForBridgeStorageLoaded';

export async function initVideoOpener(): Promise<void> {
  browser_api.runtime.onMessage.addListener((message: unknown) => {
    if (isMessageOfType(message, Actions.ShowPageLoader)) {
      document.dispatchEvent(
        new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
          detail: {
            isShown: true,
            loaderType: 'page',
          },
        }),
      );
    }
  });

  browser_api.runtime.onMessage.addListener(async (message: unknown) => {
    try {
      if (isMessageOfType(message, Actions.DirectVideoOpen)) {
        const store = await waitForBridgeStorageLoaded();
        const status = store[LocalStorageKey.Status];

        if (status === 'Free') {
          document.dispatchEvent(
            new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
              detail: {
                isShown: true,
                loaderType: 'video',
                videoPayload: message.payload,
              },
            }),
          );
        } else {
          document.dispatchEvent(
            new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
              detail: {
                isShown: true,
                loaderType: 'page',
              },
            }),
          );
          initVideoLoader(message.payload);
        }
      }
    } catch (error) {
      console.error('error', error);
    }
  });

  browser_api.runtime.onMessage.addListener(async (message: unknown) => {
    try {
      if (isMessageOfType(message, Actions.FailedVideoOpen)) {
        document.dispatchEvent(
          new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
            detail: { isShown: false },
          }),
        );

        alert(
          `${process.env.EXTENSION_TITLE}     ${message.payload.watchUrl} :(`,
        );
      }

      await browser_api.runtime.sendMessage<VideoOpenerReadyMessage>({
        action: Actions.VideoOpenerReady,
      });
    } catch (error) {
      console.error('error', error);
    }
  });
}
