import Toastify from 'toastify-js';
import { exhaustiveCheck } from '@app/utils/exhaustiveCheck';
import type { ToastInstance, ToastOptions } from '../types';
import {
  checkFullscreen,
  getActiveToasts,
  hideAllToasts,
  hideAllToastsByType,
  hideAllToastsInAllTabs,
  hideAllToastsInAllTabsByType,
  setupFullscreenHandler,
} from '../utils';
import { createToastContent } from './createToastContent';

/**
 *  toast        toastify-js
 * @param options -   toast
 * @returns  toast   hide()  show()  type
 */
export function showToast({
  closeBehavior = 'single',
  gravity = 'bottom',
  position = 'right',
  duration = -1,
  hideOnFullscreen = true,
  ...options
}: ToastOptions): ToastInstance {
  let toastInstance: ReturnType<typeof Toastify> | null = null;
  let toastInstanceResult: ToastInstance | null = null;
  let cleanupFullscreenHandler: () => void = () => {};

  const hideToast = () => {
    const activeToasts = getActiveToasts();

    if (toastInstanceResult) {
      activeToasts.delete(toastInstanceResult);
    }

    if (toastInstance && toastInstance.toastElement) {
      toastInstance.hideToast();
      cleanupFullscreenHandler();
      options.sideEffectOnClose && options.sideEffectOnClose();

      switch (closeBehavior) {
        case 'allByType':
          hideAllToastsByType(options.type);
          break;
        case 'all':
          hideAllToasts();
          break;
        case 'allInAllTabsByType':
          hideAllToastsInAllTabsByType(options.type);
          break;
        case 'allInAllTabs':
          hideAllToastsInAllTabs();
          break;
        case 'single':
          break;
        default:
          exhaustiveCheck(closeBehavior);
      }
    }
  };

  const content = createToastContent(options, hideToast);

  toastInstance = Toastify({
    node: content,
    duration,
    gravity,
    position,
    className: `custom-toast-wrapper ${options.className || ''}`.trim(),
    onClick: () => {
      if (!options.buttons || options.buttons.length === 0) {
        hideToast();
      }
    },
    offset: {
      x: 8,
      y: 10,
    },
  });

  toastInstance.showToast();

  if (toastInstance.toastElement && hideOnFullscreen) {
    checkFullscreen(toastInstance.toastElement);
    cleanupFullscreenHandler = setupFullscreenHandler(
      toastInstance.toastElement,
    );
  }

  toastInstanceResult = {
    hide: hideToast,
    show: () => {
      if (toastInstance?.toastElement?.isConnected && hideOnFullscreen)
        checkFullscreen(toastInstance.toastElement);
      toastInstance?.showToast();
    },
    type: options.type,
  };

  getActiveToasts().add(toastInstanceResult);

  return toastInstanceResult;
}
