import { Actions } from '@app/actions/shared';
import {
  type InitGiftAdvertisingScriptReadyMessage,
  isMessageOfType,
} from '@app/messages/shared';
import { browser_api } from '@app/browser_api';

import {
  GiftMessage,
  HandleGiftActionOptions,
} from '@content/scripts/giftsOnYoutubePageContent/types';
import {
  BODY_CLASS,
  CLOSE_ICON_CLASS,
  GIFT_CONTAINER_ID,
  GIFT_HIDDEN_CLASS,
  GIFT_IMAGES,
} from './constants';

let currentGiftElement: HTMLElement | null = null;

const initGiftsOnYoutubePageListener = async (
  message: unknown,
): Promise<void> => {
  if (isMessageOfType(message, Actions.InitGiftAdvertisingScript)) {
    showGiftOnPage();
  }
};

const hideGiftOnYoutubePageListener = async (
  message: unknown,
): Promise<void> => {
  if (isMessageOfType(message, Actions.HideGift)) {
    hideGiftFor24Hours();
  }
};

export async function initGiftsOnYoutubePage(): Promise<void> {
  try {
    if (
      !browser_api.runtime.onMessage.hasListener(initGiftsOnYoutubePageListener)
    ) {
      browser_api.runtime.onMessage.addListener(initGiftsOnYoutubePageListener);
    }

    if (
      !browser_api.runtime.onMessage.hasListener(hideGiftOnYoutubePageListener)
    ) {
      browser_api.runtime.onMessage.addListener(hideGiftOnYoutubePageListener);
    }

    await browser_api.runtime.sendMessage<InitGiftAdvertisingScriptReadyMessage>(
      {
        action: Actions.InitGiftAdvertisingScriptReady,
      },
    );
  } catch (error) {
    console.error(error);
  }
}

function showGiftOnPage(): void {
  hideCurrentGift();

  const randomGiftUrl =
    GIFT_IMAGES[Math.floor(Math.random() * GIFT_IMAGES.length)];

  const { giftBody, giftContainer } = createGiftElement(randomGiftUrl);
  currentGiftElement = giftContainer;
  positionGiftRandomly({ giftContainer: currentGiftElement, giftBody });

  document.documentElement.appendChild(currentGiftElement);

  // Add animation
  setTimeout(() => {
    currentGiftElement?.classList.add('youtube-gift-visible');
  }, 100);
}

function createGiftElement(imageUrl: string): {
  giftContainer: HTMLElement;
  giftBody: HTMLElement;
} {
  const giftContainer = document.createElement('div');
  giftContainer.id = GIFT_CONTAINER_ID;
  giftContainer.className = 'youtube-gift';

  const giftBody = document.createElement('div');
  giftBody.className = BODY_CLASS;

  const closeBtn = document.createElement('button');
  closeBtn.type = 'button';
  closeBtn.className = CLOSE_ICON_CLASS;

  closeBtn.innerHTML = `
    <svg width="100%" height="100%" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path opacity="0.8" d="M22 12C22 17.5228 17.5228 22 12 22C6.47715 22 2 17.5228 2 12C2 6.47715 6.47715 2 12 2C17.5228 2 22 6.47715 22 12Z" fill="#fff"/>
        <path d="M8.96967 8.96967C9.26256 8.67678 9.73744 8.67678 10.0303 8.96967L12 10.9394L13.9697 8.96969C14.2626 8.6768 14.7374 8.6768 15.0303 8.96969C15.3232 9.26258 15.3232 9.73746 15.0303 10.0304L13.0607 12L15.0303 13.9696C15.3232 14.2625 15.3232 14.7374 15.0303 15.0303C14.7374 15.3232 14.2625 15.3232 13.9696 15.0303L12 13.0607L10.0304 15.0303C9.73746 15.3232 9.26258 15.3232 8.96969 15.0303C8.6768 14.7374 8.6768 14.2626 8.96969 13.9697L10.9394 12L8.96967 10.0303C8.67678 9.73744 8.67678 9.26256 8.96967 8.96967Z" fill="black"/>
    </svg>
  `;

  const giftImage = document.createElement('img');
  giftImage.src = imageUrl;
  giftImage.alt = '';
  giftImage.className = 'youtube-gift-image';

  giftBody.appendChild(giftImage);
  giftContainer.appendChild(giftBody);
  giftContainer.appendChild(closeBtn);

  giftBody.addEventListener('click', () => {
    void handleGiftAction({ imageUrl, action: Actions.GiftClicked });
  });
  closeBtn.addEventListener('click', () => {
    void handleGiftAction({
      imageUrl,
      action: Actions.CloseGift,
    });
  });

  addGiftStyles();

  return { giftContainer, giftBody };
}

function positionGiftRandomly({
  giftContainer: element,
  giftBody,
}: {
  giftContainer: HTMLElement;
  giftBody: HTMLElement;
}): void {
  const viewportWidth = window.innerWidth;
  const viewportHeight = window.innerHeight;

  const giftSize = 150;
  const margin = 20;

  const maxX = viewportWidth - giftSize - margin;
  const maxY = viewportHeight - giftSize - margin;

  const randomX = Math.random() * Math.max(0, maxX) + margin;
  const randomY = Math.random() * Math.max(0, maxY) + margin;

  const rotationAngles = [-30, -20, -10, 0, 10, 20, 30];
  const randomRotation =
    rotationAngles[Math.floor(Math.random() * rotationAngles.length)];

  element.style.left = `${randomX}px`;
  element.style.top = `${randomY}px`;
  giftBody.style.rotate = `${randomRotation}deg`;
}

export async function handleGiftAction({
  imageUrl,
  action,
}: HandleGiftActionOptions): Promise<void> {
  try {
    await browser_api.runtime.sendMessage<GiftMessage>({
      action,
      payload: { imageUrl },
    });

    if (action === Actions.GiftClicked) {
      hideGiftFor24Hours();
    }
  } catch (error) {
    console.error('Error handling gift action:', error);
  }
}

function hideGiftFor24Hours(): void {
  if (currentGiftElement) {
    currentGiftElement.classList.add(GIFT_HIDDEN_CLASS);
    currentGiftElement.style.display = 'none';
  }
}

function hideCurrentGift(): void {
  const existingGift = document.getElementById(GIFT_CONTAINER_ID);
  if (existingGift) {
    existingGift.remove();
  }
  currentGiftElement = null;
}

function addGiftStyles(): void {
  const existingStyles = document.getElementById('youtube-gift-styles');
  if (existingStyles) {
    return;
  }

  const style = document.createElement('style');
  style.id = 'youtube-gift-styles';
  style.textContent = `
    .youtube-gift {
      cursor: pointer !important;
      position: fixed;
      width: 170px;
      height: 170px;
      z-index: 9999999;
      opacity: 0;
      transform: scale(0.5);
      transition: all 0.3s ease;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .youtube-gift::before {
      content: '';
      position: absolute;
      top: 15px;
      left: 15px;
      right: 15px;
      bottom: 15px;
      background: linear-gradient(45deg, #7EC7FF, #C97EFF);
      border-radius: inherit;
      filter: blur(8px);
      z-index: -1;
      opacity: 0.3;
    }

    .youtube-gift.youtube-gift-visible {
      opacity: 1;
      transform: scale(1);
    }

    .youtube-gift-image {
      width: 150px;
      height: 150px;
      object-fit: contain;
      animation: giftFloat 3s ease-in-out infinite;
      border-radius: 50%;
      z-index: 1;
      position: relative;
    }

    .youtube-gift:hover {
      transform: scale(1.1);
    }

    .youtube-gift-hidden {
      display: none !important;
    }

    @keyframes giftFloat {
      0%, 100% {
        transform: scale(1);
      }
      50% {
        transform: scale(1.1);
      }
    }
    
    .${CLOSE_ICON_CLASS}{
        position: absolute;
        top: 35px;
        right: 2px;
        z-index: 999;
        border: none;
        width: 28px;
        height: 28px;
        cursor: pointer;
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        background: none;
    }
    .${CLOSE_ICON_CLASS}:hover{
        opacity: 0.8;
    }
  `;

  document.head.appendChild(style);
}
